* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.card {
    background: #fff;
    border-radius: 2px;
    height: 150px;
    margin: 1rem;
    position: relative;
    width: 225px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:#fff;
  }
  .card > .img-container > img {
    width: 100%
  }
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  align-content: flex-start;
  overflow: auto;
  color: lightseagreen;
  font-family: fantasy;
  font-size: xx-large;
}
.title {
    width: 100%;
    text-align: center;
    font-size: 5rem;
    text-decoration: underline;
    color: green;
    margin-bottom: 50px;
  }
